@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@200;300;400&display=swap');

.nav {
  grid-area: n;
  font-family: 'Inconsolata', monospace;

  .nav_list {
    padding: 0;
    margin: 0;
    //width: 190px
    color: white;
    display: flex;
    flex-direction: column;

    .nav_item {
      margin-bottom: 30px;

      .nav_item_link {
        color: white;
        text-transform: uppercase;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        text-decoration: none;

        img {
          width: 30px;
          height: 30px; }
        span {
          font-size: 25px; } } }


    .nav_item:last-child {
      margin-bottom: 0; } } }

